*{
  background-color: rgb(43, 43, 43);
  text-decoration: none;
}

nav{
  color: azure;
  background-color: rgb(58, 58, 58);
  font-family: 'Montserrat', sans-serif;
  text-decoration: none;
  padding-right: 45px;
}

img.logoHeader{
  border-radius: 20px;
  object-fit: cover;
  width: 200px;
  height: 60px;
}

nav{
  background-color: rgb(58, 58, 58);
  border-radius: 22px;
  display: flex;
  align-items: center;
  text-align: center;

}

nav {
    color: azure;
    background-color: rgb(58, 58, 58);
    font-family: 'Montserrat', sans-serif;
  
  }
  
  nav > a {
    margin: 0;
    margin-left: 25px;
    padding: 10px 20px;
    color: azure;
    background-color: rgb(58, 58, 58);
    text-decoration: none;
  }
 
  nav > a.active {
    background-color: rgb(49, 49, 49);
    border-radius:  50px;
    text-decoration: none;
  }
  
.List{
    display: flex;
    justify-content: left;
    background-color: rgb(65, 65, 65);
    width: 80%;
    margin-bottom: 20px;
}

.movieInf{
    background-color: rgb(65, 65, 65);
    margin-top: 20px;
}

.List > .poster > img{
    width: 100px;
    
}

.List > .movieInf > a, p{
    padding: 10px;
    color: aliceblue;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    background-color: rgb(65, 65, 65);;
    font-size: 25px;
}

.movieInf > .movieTitle{
    
    margin-right: 15px;
    height: 10px;
    /* align-self: flex-start; */
}

p.inf{
    text-align: left;
    font-size: 14px;
    font-weight: lighter;
    height: 18px;
}
.top{
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.bigCard{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 95%;
}


.bigCard > h1   {
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    font-size: 60px;
    margin-bottom: 0px;
}

.bigCard > h3   {
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    margin-bottom: 100px;
}
.PageError{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

p{
    font-size: 40px;
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
}

img{
    width: 700px;

}
